import { DeepPartial } from '../core'
import { WalletBlock } from './wallet-block.model'
import { BlockMock } from '../core/utility.mock'
import {
  VaultWalletParetoDollarData,
  VaultWalletPoolData,
  VaultWalletPoolTokenData,
} from '../vaults'
import { WEB3_DEFAULT_ADDR } from '../web3-client'

/**
 * WalletBlock Mock
 */
export function WalletBlockMock(
  options?: DeepPartial<WalletBlock>
): WalletBlock {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'WALLET_BLOCK_ID',

    block: BlockMock(options?.block),

    walletId: options?.walletId || 'WALLET_ID',
    walletAddress: options?.walletAddress || 'WALLET_ADDRESS',

    vaultId: options?.vaultId || 'VAULT_ID',
    vaultAddress: options?.vaultAddress || 'VAULT_ADDRESS',

    balance: options?.balance || '0',
    tokenBalance: options?.tokenBalance || '0',

    paretoDollar: WalletBlockParetoDollarMock(options?.paretoDollar),
    pools: options?.pools?.map((p) => WalletBlockPoolMock(p)),

    createdAt: now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}

export function WalletBlockParetoDollarMock(
  options?: Partial<VaultWalletParetoDollarData>
): VaultWalletParetoDollarData {
  return {
    uspBalance: options?.uspBalance,
    stakedBalance: options?.stakedBalance,
  }
}

export function WalletBlockPoolMock(
  options?: DeepPartial<VaultWalletPoolData>
): VaultWalletPoolData {
  return {
    balance: options?.balance,
    lpBalance: options?.lpBalance,
    address: options?.address || WEB3_DEFAULT_ADDR,
    protocol: options?.protocol || 'Idle',
    operatorId: options?.operatorId,
    tokens: options?.tokens?.map((t) => WalletBlockPoolTokenMock(t)),
  }
}

export function WalletBlockPoolTokenMock(
  options?: Partial<VaultWalletPoolTokenData>
): VaultWalletPoolTokenData {
  return {
    tokenId: options?.tokenId,
    tokenAddress: options?.tokenAddress || WEB3_DEFAULT_ADDR,
    balance: options?.balance || '0',
    balanceScaled: options?.balanceScaled,
  }
}
